@media (max-width: $tablet-breakpoint) {
  .faq {
    padding-top: 80px;
  }

  .faq__accordeon {
    width: 100%;
  }

  .accordeon__no-js .faq__item {
    padding: 50px;
  }

  .accordeon__no-js .faq__item-title {
    padding-bottom: 35px;
  }

  .faq__item {
    padding: 15px 0;
  }

  .faq__button {
    height: 97px;
    padding: 35px 50px;
  }

  .faq__item p {
    padding: 0 100px 35px 50px;
  }
}

@media (max-width: $mobile-breakpoint) {
  .faq {
    padding-top: 70px;
  }

  .faq h2 {
    margin-bottom: 40px;
  }

  .accordeon__no-js .faq__item {
    padding: 25px;
  }

  .accordeon__no-js .faq__item-title {
    padding-bottom: 25px;
    font-size: 16px;
    line-height: 22.4px;
  }

  .faq__button {
    padding: 25px;
    font-size: 16px;
    line-height: 22.4px;
  }

  .faq__item p {
    padding: 0 25px 25px;
    font-size: 14px;
    line-height: 19.6px;
  }
}
